@import url("https://fonts.googleapis.com/css2?family=Economica&family=Grape+Nuts&family=Roboto:wght@100;300;400;700;900&display=swap");

* {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}

*::-webkit-scrollbar {
  display: none;
}

a,
h2 {
  color: white;
}

a {
  text-decoration: none;
}

.header {
  height: 100px;
  background-color: rgb(16, 16, 16);
  display: flex;
  align-items: center;
  padding: 2rem;
  box-sizing: border-box;
  justify-content: space-between;
}

.header img {
  height: 100%;
  cursor: pointer;
}

.header div {
  width: 100%;
  margin-left: 4vmax;
}

.header div a {
  margin: 2vmax;
  font-size: 1.3rem;
  font-weight: 300;
  transition: color 0.3s;
}

.header div a:hover {
  color: rgb(200, 200, 200);
}

.header svg {
  color: white;
  font-size: 2rem;
  cursor: pointer;
}

.header svg:hover {
  color: rgb(216, 216, 216);
}
